import React, {useContext, useState} from "react";
import { Link } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import SocialNetLinks from "./SocialNetLinks";
import VersesContext from "../contexts/BibleContext";
import {SearchIcon, CloseIcon} from "./AppIcons";
import { collapseMenu } from "./OverlayMenu";
import { shareUrl, getBook } from "../Utilities";

function SearchTerm() {
    const {verses, searchVerses, books} = useContext(VersesContext);

    const [sstring, setSstring] = useState("");
    const [searchRequested, setSearchRequested] = useState(false);

    const updateTerm = (event) => {
        const { value } = event.target;
        setSstring(value);
    };

    const findTerm = () => {
        setSearchRequested(true);
        if (sstring.trim() !== "") {
            searchVerses(sstring)
        }
    }

    const handleKeyPress = (event) => {
        if (event.key === "Enter") {
            collapseMenu();
            findTerm();
        }
    }

    return(
        <div>
             <div className="navbar">
                 <div className="nav-item vertical-center">
                    <input
                        type="text"
                        placeholder="upišite pojam"
                        onChange={updateTerm}
                        onKeyPress={handleKeyPress}
                        onClick={collapseMenu}
                    />
                    <button onClick={findTerm}>
                        <SearchIcon />
                    </button>
                 </div>
                 <div className="close-page"><CloseIcon /></div>
             </div>
             <div className="chapter">
                <div style={{ 
                        width: "70%",
                        margin: "7rem auto 2rem auto",
                        display: "flex", 
                        justifyContent: "center" 
                        }}>
                    {
                        searchRequested ?
                        <p>Pronađeno: {verses.length} rezultata</p> :
                        <small style={{textDecoration: "underline"}}>
                            (Jednostavna pretraga, pazite na padeže, velika i mala slova)
                        </small>
                    }
                </div>
                {
                    verses.map(item => (
                    <div className="verses-list" onClick={collapseMenu} key={uuidv4()}>
                        <div className="verse-detail">
                            <SocialNetLinks
                                shareUrl={shareUrl}
                                verseLocationAndText={`"${item.verse_text}" \n\t ~ ${getBook(item.book).name} ${item.chapter}:${item.verse_no}\n\n`}
                                twitterVerseLocation={`${item.book}_${item.chapter}_${item.verse_no}`}
                                title={"Citat iz Biblije"}
                            />
                            <Link 
                                to={`/verse/${item.book}/${item.chapter}/${item._id}`}
                            >
                                <div>
                                    <div>
                                        <p>{item.verse_text}</p> 
                                    </div>
                                    <small>~{
                                        (books.filter(
                                            (elmt) => (elmt.eng_short === item.book))[0].name)
                                        }
                                        <em>
                                            {item.chapter}:{item.verse_no}
                                        </em>
                                    </small>
                                    <hr></hr>
                                </div>  
                            </Link>
                        </div>
                    </div>
                    ))
                }
            </div>
        </div>
    )
}

export default SearchTerm;